<template>
  <div class="header-index">
    <!--logo 及 后台管理系统名称-->
    <div
      class="left"
      v-show="!isCollapse"
      :class="{ 'left-no-collapse': !isCollapse }"
    >
      <img :src="logoImg" alt="" />
      <span>前端生态</span>
    </div>

    <div
      class="left"
      v-show="isCollapse"
      :class="{ 'left-collapse': isCollapse }"
    >
      <img :src="logoImg" alt="" />
    </div>

    <!--折叠菜单 及 面包屑-->
    <div
      class="center"
      :class="{
        'center-no-collapse': !isCollapse,
        'center-collapse': isCollapse,
      }"
    >
      <!--折叠菜单-->
      <div class="collapse">
        <i
          :class="{
            'el-icon-s-unfold': isCollapse,
            'el-icon-s-fold': !isCollapse,
          }"
          type="text"
          @click="handleCollapse"
        ></i>
      </div>

      <!--导航栏--面包屑-->
      <div class="breadcrumb">
        <el-breadcrumb separator="/">
          <el-breadcrumb-item :to="{ path: '/home' }">首页</el-breadcrumb-item>
          <el-breadcrumb-item>{{ $route.meta.title }}</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
    </div>

    <!--头部右边-->
    <div class="right">
      <div class="right-operate">
        <i class="el-icon-s-operation"></i>
        <i class="el-icon-picture-outline-round" @click="jumpOffWebsite"></i>
      </div>
      <!--退出登录-->
      <div class="out-login">
        <el-avatar icon="el-icon-user-solid"></el-avatar>
        <el-dropdown @command="handleCommand">
          <span class="el-dropdown-link">
            {{ userName }}<i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item command="out">退出登录</el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isCollapse: false,
      userName: localStorage.getItem("userName"),
      logoImg: require("../assets/images/logo6.png"),
    };
  },
  methods: {
    //点击折叠
    handleCollapse() {
      this.isCollapse = !this.isCollapse;
      this.$bus.$emit("isCollapse", this.isCollapse);
    },

    /**退出登录 */
    handleCommand(command) {
      let out = command == "out";
      if (!out) return;
      this.$router.push("/login");
      localStorage.removeItem("userName");
    },

    /**跳转官网 */
    jumpOffWebsite() {
      this.$router.push("/offWebsite");
    },
  },
};
</script>

<style lang="scss" scoped>
@mixin flex {
  display: flex;
  align-items: center;
  justify-content: center;
}
.header-index {
  height: 50px;
  width: 100%;
  color: #fff;
  background-color: #001529;
  display: flex;
  align-items: center;
  .collapse {
    margin: 30px;
    font-size: 20px;
    color: #fff;
    text-align: center;
    cursor: pointer;
  }

  .left {
    height: inherit;
    display: flex;
    align-items: center;
    justify-content: center;
  }

  .left-collapse {
    flex: 70px;
    margin: 0 auto;

    img {
      height: 30px;
      width: 70px;
      width: 70px !important;
    }
  }

  .left-no-collapse {
    flex: 200px;
    display: flex;
    justify-content: space-around;
    img {
      height: 30px !important;
      width: 70px;
    }
    span {
      font-family: "Franklin Gothic Medium", "Arial Narrow", Arial, sans-serif;
      color: #fff;
    }
  }

  .center {
    height: inherit;
    display: flex;
    align-items: center;
    color: #fff;
  }

  .center-no-collapse {
    flex: calc(100% - 500px);
  }

  .center-collapse {
    flex: calc(100% - 370px);
  }

  .right {
    flex: 300px;
    height: inherit;
    display: flex;
    justify-content: space-between;
    align-items: center;
    box-sizing: border-box;
    padding: 0 10px;
    .right-operate {
      font-size: 20px;
      i {
        margin: 0 5px;
      }
    }
    .out-login {
      @include flex;
      .el-dropdown {
        margin-left: 0.5rem;
      }
    }
  }

  /deep/.el-breadcrumb {
    .el-breadcrumb__inner {
      color: #fff !important;
    }
  }
}
</style>